<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12" class="mt-16">
        <h1> Vue Assigment<br> Data in Tabular format</h1>
        <div v-if="inProgress">
          <v-progress-circular indeterminate color="primary" :size="50" class="mt-10"></v-progress-circular>
        </div>
        <div v-else>
          <v-data-table :headers="headers" :items="getData()" :items-per-page="5" class="elevation-3">
            <template  v-slot:[`item.DateTime`]="{ item }">
              <span>{{ stringToNormalDateTime(item.DateTime) }}</span>
            </template>
          </v-data-table>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  /* eslint-disable no-alert, no-console ,  no-debugger */
  import { createHelpers } from "vuex-map-fields";
  const { mapFields } = createHelpers({
    getterType: "DemoList/getField",
    mutationType: "DemoList/updateField"
  });

  export default {
    name: 'DemoList',
    data: () => ({
      headers: [
        {
          text: 'Date    Time',
          align: 'start',
          sortable: false,
          value: 'DateTime',
        },
        {
          text: 'Open',
          value: '1Open',
          align: 'start',
          sortable: false,
        },
        {
          text: 'High',
          value: '2High',
          align: 'start',
          sortable: false,
        },
        {
          text: 'low',
          value: '3Low',
          align: 'start',
          sortable: false,
        },
        {
          text: 'Close',
          value: '4Close',
          align: 'start',
          sortable: false,
        },
        {
          text: 'Volume',
          value: '5Volume',
          align: 'start',
          sortable: false,
        },
      ],
    }),

    created() {
      this.$store.dispatch("DemoList/fetchList");
    },
    computed: {
      ...mapFields(["list"]),
      inProgress: function () {
        return this.$store.state.DemoList.inProgress;
      },
    },
    methods: {
      getData() {
        if (this.list && this.list.timeSeries5Min) {
          const timeSeries = Object.values(this.list.timeSeries5Min)
          let timeSeries5Min = Object.keys(this.list.timeSeries5Min).map(function (ele) {
            return { "DateTime": ele }
          })
          let mergedArray = [];
          for (let i = 0; i < timeSeries.length; i++) {
            mergedArray.push({
              ...timeSeries[i],
              ...timeSeries5Min[i]
            });
          }
          return mergedArray
        }
      },
      stringToNormalDateTime(date) {
      let newDateTime = date.slice(0,4)+'-'+date.slice(4,6)+'-'+date.slice(6,8)+' ' + date.slice(8,10)+":"+date.slice(10,12)+":"+date.slice(12,14)
       return newDateTime;
    },
    }
  }
</script>